.impressum {
  display: flex;
  justify-content: center;
  align-items: center;

  &__title {
    font-size: 40px;
  }

  &__container {
    flex-direction: column;
    min-height: 100vh;
    padding: 100px 0 0 0;
    background: none;
  }
  &__name {
    font-size: 30px;
    margin: 30px 0 10px;
  }

  &__info {
    font-size: 20px;
    margin: 0 0 10px 0;
  }

  &__sub-info {
    margin: 20px 0 0 0;
    font-size: 20px;
  }

  &__email {
    font-size: 20px;
    margin: 20px 0;
    color: #fff;
    cursor: default;
    &_adress {
      color: #0178ff;
      cursor: pointer;
    }
    &_yellow {
      display: block;
      color: #ff9901;
      cursor: pointer;
      margin: 0 10px;
      font-size: 19px;
    }
  }
}

